import storeupList from '@/views/pages/storeup/list'
import cheweixinxiList from '@/views/pages/cheweixinxi/list'
import cheweixinxiDetail from '@/views/pages/cheweixinxi/formModel'
import cheweixinxiAdd from '@/views/pages/cheweixinxi/formAdd'
import louyuxinxiList from '@/views/pages/louyuxinxi/list'
import louyuxinxiDetail from '@/views/pages/louyuxinxi/formModel'
import louyuxinxiAdd from '@/views/pages/louyuxinxi/formAdd'
import gonggaoxinxiList from '@/views/pages/gonggaoxinxi/list'
import gonggaoxinxiDetail from '@/views/pages/gonggaoxinxi/formModel'
import gonggaoxinxiAdd from '@/views/pages/gonggaoxinxi/formAdd'
import fuwuleixingList from '@/views/pages/fuwuleixing/list'
import fuwuleixingDetail from '@/views/pages/fuwuleixing/formModel'
import fuwuleixingAdd from '@/views/pages/fuwuleixing/formAdd'
import fuwuxinxiList from '@/views/pages/fuwuxinxi/list'
import fuwuxinxiDetail from '@/views/pages/fuwuxinxi/formModel'
import fuwuxinxiAdd from '@/views/pages/fuwuxinxi/formAdd'
import fuwuyuyueList from '@/views/pages/fuwuyuyue/list'
import fuwuyuyueDetail from '@/views/pages/fuwuyuyue/formModel'
import fuwuyuyueAdd from '@/views/pages/fuwuyuyue/formAdd'
import feiyongxinxiList from '@/views/pages/feiyongxinxi/list'
import feiyongxinxiDetail from '@/views/pages/feiyongxinxi/formModel'
import feiyongxinxiAdd from '@/views/pages/feiyongxinxi/formAdd'
import renwufenpeiList from '@/views/pages/renwufenpei/list'
import renwufenpeiDetail from '@/views/pages/renwufenpei/formModel'
import renwufenpeiAdd from '@/views/pages/renwufenpei/formAdd'
import weixiuwanchengList from '@/views/pages/weixiuwancheng/list'
import weixiuwanchengDetail from '@/views/pages/weixiuwancheng/formModel'
import weixiuwanchengAdd from '@/views/pages/weixiuwancheng/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'weixiuyuanList',
			component: weixiuyuanList
		}, {
			path: 'weixiuyuanDetail',
			component: weixiuyuanDetail
		}, {
			path: 'weixiuyuanAdd',
			component: weixiuyuanAdd
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'gonggongsheshiList',
			component: gonggongsheshiList
		}, {
			path: 'gonggongsheshiDetail',
			component: gonggongsheshiDetail
		}, {
			path: 'gonggongsheshiAdd',
			component: gonggongsheshiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'cheweixinxiList',
			component: cheweixinxiList
		}, {
			path: 'cheweixinxiDetail',
			component: cheweixinxiDetail
		}, {
			path: 'cheweixinxiAdd',
			component: cheweixinxiAdd
		}
		, {
			path: 'louyuxinxiList',
			component: louyuxinxiList
		}, {
			path: 'louyuxinxiDetail',
			component: louyuxinxiDetail
		}, {
			path: 'louyuxinxiAdd',
			component: louyuxinxiAdd
		}
		, {
			path: 'gonggaoxinxiList',
			component: gonggaoxinxiList
		}, {
			path: 'gonggaoxinxiDetail',
			component: gonggaoxinxiDetail
		}, {
			path: 'gonggaoxinxiAdd',
			component: gonggaoxinxiAdd
		}
		, {
			path: 'fuwuleixingList',
			component: fuwuleixingList
		}, {
			path: 'fuwuleixingDetail',
			component: fuwuleixingDetail
		}, {
			path: 'fuwuleixingAdd',
			component: fuwuleixingAdd
		}
		, {
			path: 'fuwuxinxiList',
			component: fuwuxinxiList
		}, {
			path: 'fuwuxinxiDetail',
			component: fuwuxinxiDetail
		}, {
			path: 'fuwuxinxiAdd',
			component: fuwuxinxiAdd
		}
		, {
			path: 'fuwuyuyueList',
			component: fuwuyuyueList
		}, {
			path: 'fuwuyuyueDetail',
			component: fuwuyuyueDetail
		}, {
			path: 'fuwuyuyueAdd',
			component: fuwuyuyueAdd
		}
		, {
			path: 'feiyongxinxiList',
			component: feiyongxinxiList
		}, {
			path: 'feiyongxinxiDetail',
			component: feiyongxinxiDetail
		}, {
			path: 'feiyongxinxiAdd',
			component: feiyongxinxiAdd
		}
		, {
			path: 'renwufenpeiList',
			component: renwufenpeiList
		}, {
			path: 'renwufenpeiDetail',
			component: renwufenpeiDetail
		}, {
			path: 'renwufenpeiAdd',
			component: renwufenpeiAdd
		}
		, {
			path: 'weixiuwanchengList',
			component: weixiuwanchengList
		}, {
			path: 'weixiuwanchengDetail',
			component: weixiuwanchengDetail
		}, {
			path: 'weixiuwanchengAdd',
			component: weixiuwanchengAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
